.pageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  
}

.signupCard {
  display: flex;
  height: 85%;
  width: 90%;
  background: radial-gradient(circle at center, #849AB0 55%, #2D70A7);
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  overflow: hidden;

}

.imageSection {
  flex: 1;
  background: #f0f0f0;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.formSection {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 justify-content: flex-start;
}

.title {
  font-size: 28px;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 10px;
}

.subTitle {
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 20px;
}

.link {
  color: #3366cc;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.input {
  width: 60vh;
  height: 4vh;
  padding: 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 6px;

}

.input:focus {
  border-color: #3366cc;
  box-shadow: 0 0 5px rgba(51, 102, 204, 0.5);
  outline: none;
}

.button {
  background: #0066ff;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  padding: 12px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s;
}

.button:hover {
  background: #0055cc;
}

.a{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #3366cc;
  text-decoration: none;
}

.footer {
  margin-top: 20px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  font-size: 14px;
}

.footerLogo {
  width: 100px;
  height: 170px;
}
