.appBar {
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  justify-content: center;
  padding: 10px 0;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1200px;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.logo img {
  height: 30px;
}

.nav {
  display: flex;
  align-items: center;
}

.dropdownSelect {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #0066ff;
  color: white;
  font-size: 16px;
  font-weight: 500;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.dropdownSelect option {
  color: black;
  background: white;
}

.authLinks {
  display: flex;
  gap: 20px; 
}

.authLink {
  background: #0066ff;
  color: white;
  font-size: 16px;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 5px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10vh;
}

.authLink:hover {
  background: #0056d4;
}
