.pageContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    font-family: Arial, sans-serif;
    border: 1px solid;
    width: 110%;
    height: 100%;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .grid {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 20px;
  }
  
  .sidebar {
    background: #f9f9f9;
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  
  }
  
  .sidebar h2 {
    margin-bottom: 10px;
    font-size: 1.2rem;
  }
  
  .topicList {
    list-style: none;
    padding: 0;
  }
  
  .topicList li {
    margin-bottom: 10px;
  }
  
  .topicLink {
    text-decoration: none;
    color: #333;
    font-size: 1rem;
    text-transform: capitalize;
  }
  
  .topicLink:hover {
    color: #007bff;
  }
  
  .mainContent {
    background: #fff;
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .threadContainer {
    margin-top: 10px;
  }
  
  .loaderText,
  .endMessage {
    text-align: center;
    margin-top: 20px;
    font-size: 1rem;
  }
  
  .topThread {
    margin-bottom: 15px;
  }
  
  .threadLink {
    text-decoration: none;
    color: #000;
  }
  
  .threadCard {
    background: #f1f1f1;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: background 0.3s ease;
  }
  
  .threadCard:hover {
    background: #e9ecef;
  }
  
  .threadCreator,
  .threadReplyCount {
    font-size: 0.9rem;
    color: #666;
  }
  